import { WordIndicatorState } from '../../constants';
import { ObservableModel } from '../observable-model';

export class WordIndicatorModel extends ObservableModel {
  constructor() {
    super('WordIndicatorModel');

    this._parts = null;
    this._state = WordIndicatorState.Idle;

    this.makeObservable('_parts', '_state');
  }

  get text() {
    return this._parts.map(p => p.value).join('');
  }

  get parts() {
    return this._parts;
  }

  get state() {
    return this._state;
  }

  clear() {
    this._parts = [];
  }

  alert() {
    this._state = WordIndicatorState.Alert;
  }

  idle() {
    this._state = WordIndicatorState.Idle;
  }

  addPart(part) {
    this._parts = [...this._parts, part];
  }

  removePart(part) {
    this._parts = this._parts.filter(p => p !== part);
  }

  initialize() {
    this._parts = [];
  }
}
