/* eslint-disable max-classes-per-file */
/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { getCtaEmbeddedPlayButtonConfig, getEmbeddedCTAButtonConfig } from '../../configs/button-configs';
import { getCTAEmbeddedGridConfig } from '../../configs/grid-configs';
import { getHandImageConfig } from '../../configs/image-configs';
import { getCtaConfettiEmitterConfig } from '../../configs/particles-configs';
import { getEmbeddedCtaSubTitleTextConfig, getEmbeddedCtaTitleTextConfig } from '../../configs/text-configs';
import { ViewEvents } from '../../events/view-events';
import { handMoveTw, handScaleTw, makeEmitter, makeImage, makeText, postRunnable } from '../../utils';
import { Container } from '../../utils/container';
import { BackgroundComponent } from '../game/menu/background-component';
import { CTABaseView } from './cta-base-view';

const buttonsConfig = [
  {
    name: 'Music',
    width: 355,
    cell: 'button_1'
  },
  {
    name: 'Food',
    width: 355,
    cell: 'button_2'
  },
  {
    name: 'Geography',
    width: 420,
    cell: 'button_3'
  }
];

const off = {
  x: 80,
  y: 20
};

class EmbeddedCtaTitle extends Container {
  constructor() {
    super();

    this._build();
  }

  _build() {
    this.addChild((this._title = this._buildTitle()));
    this.addChild((this._subtitle = this._buildSubTitle()));

    this._title.anchor.set(0.5, 1);
    this._subtitle.anchor.set(0.5, 0);
  }

  _buildTitle() {
    return makeText(getEmbeddedCtaTitleTextConfig());
  }

  _buildSubTitle() {
    return makeText(getEmbeddedCtaSubTitleTextConfig());
  }
}

export class CTAEmbeddedView extends CTABaseView {
  build() {
    super.build(getCtaEmbeddedPlayButtonConfig());

    this.buildBg();
    this.alpha = 0;
    this._buildParticles();
    this._buildTitle();
    this._buildButtons();
    this.bringToTop(this.playButton);

    this._buildHand();
    this.show();
    postRunnable(() => {
      this._setHandNag();
    });
  }

  getGridConfig() {
    return getCTAEmbeddedGridConfig();
  }

  buildBg() {
    this._bg = new BackgroundComponent();
    this.setChild('bg', this._bg);
  }

  buildBlocker() {
    super.buildBlocker(0);
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }

    super.rebuild(config);

    if (this._hand) {
      this._destroyHand();
      this._buildHand();
      postRunnable(() => {
        this._setHandNag();
      });
    }
  }

  _buildParticles() {
    makeEmitter(getCtaConfettiEmitterConfig(this, -50));
  }

  _buildButtons() {
    this._buttons = buttonsConfig.map(c => {
      const { name, width, cell } = c;
      const btn = this.buildButton(getEmbeddedCTAButtonConfig(name, width), () => {
        lego.event.emit(ViewEvents.EmbeddedCtaView.MenuBtnClick, name);
      });
      btn.name = name;
      this.setChild(cell, btn);

      return btn;
    });

    this._points = this._getButtonsPositions();
  }

  _buildHand() {
    const hand = makeImage(getHandImageConfig());
    hand.scale.x *= -1;
    this.addChild((this._hand = hand));
  }

  _setHandNag() {
    this._showBetweenHint();
  }

  _buildTitle() {
    this.setChild('title', new EmbeddedCtaTitle());
  }

  _showBetweenHint() {
    this._removeHandTw();
    this._currentPoint = 0;
    const point = this._points[this._currentPoint];
    this._hand.position.set(point.x + off.x, point.y + off.y);
    this.add(this._hand);
    this._pointHand();
  }

  _pointHand() {
    handScaleTw(this._hand).onComplete.addOnce(() => {
      this._currentPoint += 1;
      if (this._currentPoint === this._points.length) {
        this._currentPoint = 0;
      }
      this._moveHand(this._points[this._currentPoint]);
    });
  }

  _moveHand(pos) {
    handMoveTw(this._hand, pos, off.x, off.y).onComplete.addOnce(() => {
      this._pointHand();
    });
  }

  _getButtonsPositions() {
    const positions = [];

    this._buttons.forEach(btn => {
      positions.push(btn.position);
    });

    return positions;
  }

  _destroyHand() {
    this._removeHandTw();
    this._hand.destroy();
    this._hand = null;
  }

  _removeHandTw() {
    this.game.tweens.removeFrom(this._hand);
    this.game.tweens.removeFrom(this._hand.scale);
  }
}
