import { lego } from '@armathai/lego';
import { hintModelGuard } from '../../../guards/ad/hint-model-guard';
import { persistentCtaParamGuard } from '../../../guards/ad/persistent-cta-param-guard';
import { startHintVisibilityTimerCommand } from '../hint/start-hint-visibility-timer-command';
import { initializePersistentCtaModelCommand } from '../pcta/initialize-persistent-cta-model-command';
import { destroyTutorialModelCommand } from './destroy-tutorial-model-command';

export function onTutorialCompleteCommand() {
  lego.command
    //
    .execute(destroyTutorialModelCommand)

    .guard(hintModelGuard)
    .execute(startHintVisibilityTimerCommand)

    .guard(persistentCtaParamGuard)
    .execute(initializePersistentCtaModelCommand);
}
