import { ObservableModel } from '../observable-model';

export class WordModel extends ObservableModel {
  constructor(config) {
    super('WordModel');

    this._config = config;
    this._isCompleted = false;
    this._full = null;
    this._parts = [];

    this.makeObservable();
  }

  get config() {
    return this._config;
  }

  get isCompleted() {
    return this._isCompleted;
  }

  set isCompleted(value) {
    this._isCompleted = value;
  }

  get full() {
    return this._full;
  }

  get parts() {
    return this._parts;
  }

  initialize() {
    const { full, parts } = this._config;

    this._full = full;
    this._parts = [...parts];
  }
}
