/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCTAStandardGridConfig } from '../../configs/grid-configs';
import { getHandImageConfig } from '../../configs/image-configs';
import { InputPriority } from '../../constants';
import { ViewEvents } from '../../events/view-events';
import { makeImage, makePixel, pulse } from '../../utils';
import { Button } from '../../utils/button/button';
import { Container } from '../../utils/container';

export class CTABaseView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._blocker = null;
  }

  getGridConfig() {
    return getCTAStandardGridConfig();
  }

  rebuild(config = this.getGridConfig()) {
    if (!this.grid) {
      return;
    }
    this.game.tweens.removeFrom(this.playButton);
    super.rebuild(config);
  }

  show() {
    const tw = this.game.add.tween(this).to({ alpha: 1 }, 500, Phaser.Easing.Cubic.Out, true);
    tw.onComplete.add(() => {
      pulse(this.playButton);
      pulse(this.hand, 0.25);
    });
  }

  buildPlayButton(config) {
    return this.buildButton(config, this.onPlayClick);
  }

  buildRetryButton(config) {
    return this.buildButton(config, this.onRetryClick);
  }

  buildButton(config, callback) {
    const btn = new Button(config);
    btn.onClick.add(callback, this);
    return btn;
  }

  build(playButtonConfig) {
    super.build(this.getGridConfig());
    this.buildBlocker(0.7);
    this._buildButton(playButtonConfig);
  }

  buildHand() {
    const hand = makeImage(getHandImageConfig());
    hand.position.set(100, 20);
    hand.scale.x *= -1;
    return hand;
  }

  _buildButton(playButtonConfig) {
    this.button = new Container();
    this.button.addChild((this.playButton = this.buildPlayButton(playButtonConfig)));
    this.button.addChild((this.hand = this.buildHand()));
    this.setChild('play_button', this.button);
  }

  onScreenClick() {
    lego.event.emit(ViewEvents.CtaView.ScreenClick);
  }

  onPlayClick() {
    lego.event.emit(ViewEvents.CtaView.PlayClick);
  }

  onRetryClick() {
    lego.event.emit(ViewEvents.CtaView.RetryClick);
  }

  buildBlocker(alpha) {
    this._blocker = makePixel({ alpha });
    this._blocker.inputEnabled = true;
    this._blocker.input.priorityID = InputPriority.Cta;
    this._blocker.name = this.name;
    this.setChild('blocker', this._blocker);

    if (CI_API.Globals.PARAMS.cta_scrn_clickable) {
      this._blocker.events.onInputDown.add(this.onScreenClick, this);
    }
  }
}
