import { ObservableModel } from '../observable-model';
import { PartModel } from './part-model';
import { WordIndicatorModel } from './word-indicator-model';
import { WordModel } from './word-model';

export class LevelModel extends ObservableModel {
  constructor(config) {
    super('LevelModel');

    this._config = config;
    this._categoryId = null;
    this._words = null;
    this._parts = null;
    this._wordIndicator = null;

    this.makeObservable('_categoryId', '_words', '_parts', '_wordIndicator');
  }

  get categoriesConfig() {
    return [...this._config.categories];
  }

  get categoryConfig() {
    return this.getCategoryConfigById(this._categoryId);
  }

  get categoryId() {
    return this._categoryId;
  }

  get words() {
    return this._words;
  }

  get parts() {
    return this._parts;
  }

  get wordIndicator() {
    return this._wordIndicator;
  }

  getPartByUuid(uuid) {
    return this._parts.find(part => part.uuid === uuid);
  }

  getPartByValue(value) {
    return this._parts.find(part => part.value === value);
  }

  getWordByPartValue(value) {
    return this._words.find(word => word.parts.map(part => part.value).includes(value));
  }

  getCategoryConfigById(id) {
    return this.categoriesConfig.find(config => config.id === id);
  }

  getNotCompletedWords() {
    return this._words.filter(word => !word.isCompleted);
  }

  isPartTheStartOfWord(part) {
    const { value } = part;
    return this._words.find(word => word.parts[0].value === value);
  }

  removePart(partUuid) {
    const part = this.getPartByUuid(partUuid);
    this._parts.splice(this._parts.indexOf(part), 1);
  }

  checkWordComplete() {
    const { text, parts } = this._wordIndicator;

    for (let i = 0; i < this._words.length; i += 1) {
      const word = this._words[i];
      if (word.full === text) {
        word.isCompleted = true;
        this._wordIndicator.clear();
        parts.forEach(p => {
          p.collect();
        });

        return;
      }
    }

    this._wordIndicator.alert();
  }

  setCategory(id) {
    const { words } = this.getCategoryConfigById(id);

    this._categoryId = id;

    this._initWords(words);
    this._initParts(words);
    this._initWordIndicator();
  }

  _initWords(words) {
    this._words = words.map(wordConfig => {
      const word = new WordModel(wordConfig);
      word.initialize();

      return word;
    });
  }

  _initParts(words) {
    const parts = [];

    words.forEach(wordConfig => {
      const { parts: partsConfig } = wordConfig;
      partsConfig.forEach(part => {
        const partModel = new PartModel(part);
        partModel.initialize();
        parts.push(partModel);
      });
    });

    this._parts = parts;
  }

  _initWordIndicator() {
    this._wordIndicator = new WordIndicatorModel();
    this._wordIndicator.initialize();
  }
}
