import { lego } from '@armathai/lego';
import { store } from '../../../models/store';
import { selectWordPartCommand } from './select-word-part-command';

export function addPartToWordIndicatorCommand(uuid) {
  const { currentLevel } = store.game;
  const { wordIndicator } = currentLevel;
  const part = currentLevel.getPartByUuid(uuid);

  wordIndicator.addPart(part);
  const { parts } = wordIndicator;

  if (parts.length >= 4) {
    const removedPart = parts[0];
    lego.command.payload(removedPart.uuid).execute(selectWordPartCommand);
  }
}
