/* eslint-disable class-methods-use-this */
import { getWordPartBubbleActiveImageConfig } from '../../../configs/image-configs';
import { getWordPartTextConfig } from '../../../configs/text-configs';
import { InputPriority } from '../../../constants';
import { fitText, makeImage, makeText, searchAtlasByFrame } from '../../../utils';

export class PartComponent extends Phaser.Sprite {
  constructor(uuid, value, theme, scale) {
    const texture = searchAtlasByFrame(`level/themes/${theme}/bubble_bg_inactive.png`);
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.anchor.set(0.5);
    this.scale.set(scale);

    this._uuid = uuid;
    this._inactive = null;
    this._active = null;

    this.onClick = new Phaser.Signal();

    this._build(value, theme);
    this.deselect();
  }

  get uuid() {
    return this._uuid;
  }

  select() {
    this._active.visible = true;
  }

  deselect() {
    this._active.visible = false;
  }

  switchInput(value) {
    this._hitArea.inputEnabled = value;
  }

  _onClick() {
    this.onClick.dispatch(this._uuid);
  }

  _build(value, theme) {
    this._buildBg(theme);
    this._buildLabel(value);
    this._buildHitArea();
  }

  _buildBg(theme) {
    this._active = makeImage(getWordPartBubbleActiveImageConfig(theme));
    this.addChild(this._active);
  }

  _buildLabel(value) {
    this._label = makeText(getWordPartTextConfig(value));
    this._label.anchor.set(0.5);
    fitText(this._label, this._active.width * 0.8);
    this.addChild(this._label);
  }

  _buildHitArea() {
    const gr = this.game.add.graphics();
    gr.beginFill(0xff0000, 0);
    gr.drawCircle(0, 0, 149);
    gr.endFill();
    gr.inputEnabled = true;
    gr.input.priorityID = InputPriority.Game;
    gr.name = 'word_bubble';
    gr.events.onInputDown.add(this._onClick, this);

    this.addChild((this._hitArea = gr));
  }
}
