// TUTORIAL
export function getTutorialTextConfig(text) {
  return {
    text,
    style: {
      font: FONT2,
      fontSize: 48,
      fill: '#ffffff',
      align: 'center',
      stroke: '#000000',
      strokeThickness: 2,
      wordWrap: true,
      wordWrapWidth: 380,
      shadow: {
        x: 1,
        y: 2,
        color: '#000000',
        blur: 2,
        shadowStroke: false,
        shadowFill: true
      }
    }
  };
}

