import { GameModelState } from '../../constants';
import { randomInt } from '../../utils/number/random-int';
import { ObservableModel } from '../observable-model';
import { LevelModel } from './level-model';

export class GameModel extends ObservableModel {
  constructor(config) {
    super('GameModel');

    this._config = config;
    this._levels = null;
    this._currentLevelIndex = null;
    this._state = GameModelState.Unknown;

    this.makeObservable('_levels', '_currentLevelIndex', '_state');
  }

  get currentLevelIndex() {
    return this._currentLevelIndex;
  }

  get currentLevel() {
    return this._levels[this._currentLevelIndex];
  }

  get levels() {
    return this._levels;
  }

  get state() {
    return this._state;
  }

  set state(value) {
    this._state = value;
  }

  initialize() {
    const { levels } = this._config;

    this._levels = levels.map(levelConfig => {
      const level = new LevelModel(levelConfig);
      level.initialize();

      return level;
    });

    this._currentLevelIndex = 0;

    if (CI_API.Globals.PARAMS.skip_intro) {
      this._state = GameModelState.GamePlay;
      this.currentLevel.setCategory(`category_0_${randomInt(0, 2)}`);
    } else {
      this._state = GameModelState.ChooseCategory;
    }
  }
}
