import { ASSETS } from '../constants';
import { getGameBounds } from '../utils';

const confettiLeft = 'confettiLeft';

function getCtaConfettiParticlesConfig() {
  return {
    key: confettiLeft,
    data: {
      lifespan: 4000,
      image: ASSETS,
      frame: [
        'particles/confetti_1.png',
        'particles/confetti_2.png',
        'particles/confetti_3.png',
        'particles/confetti_4.png',
        'particles/confetti_5.png'
      ],
      vy: { min: 0.5, max: 1 },
      vx: { min: -1, max: 1 },
      anchorX: 0.5,
      anchorY: 0.5,
      rotation: { delta: { min: -Math.PI * 1, max: Math.PI * 1 } }
    }
  };
}

export const particles = [getCtaConfettiParticlesConfig()];

export function getCtaConfettiEmitterConfig(parent, y) {
  const { x, width, height } = getGameBounds();
  return {
    key: confettiLeft,
    parent,
    x: x - 50,
    y,
    force: { x: 0, y: 0.05 },
    config: {
      total: 5,
      repeat: -1,
      frequency: 50,
      zone: CI_API.game.particleStorm.createLineZone(x, y, x + Math.max(width, height) + 100, y)
    }
  };
}
