/* eslint-disable class-methods-use-this */
import { getWordIndicatorBgPatchConfig } from '../../../configs/ninepatch-configs';
import { getLevelWordIndicatorTextConfig } from '../../../configs/text-configs';
import { makeNinePatch, makeText } from '../../../utils';
import { Container } from '../../../utils/container';

export class WordIndicatorComponent extends Container {
  constructor(theme) {
    super();

    this._bg = null;
    this._label = null;

    this._build(theme);
  }

  get name() {
    return 'WordsIndicatorComponent';
  }

  updateText(text) {
    this._label.text = {
      text,
      ignoreLocalization: true,
      toString: function toString() {
        return this;
      }
    };
    if (!text || !text.length) {
      this.alpha = 0;
    } else {
      this.alpha = 1;
      this._resizeBg();
    }
  }

  alert() {
    const { x } = this;
    const tw = this.game.add.tween(this).from({ x: x + 10 }, 30, Phaser.Easing.Linear.None, true, 200, 4, true);

    return tw;
  }

  _resizeBg() {
    const { width } = this._label;
    this._bg.resize(Math.max(width + 70, 150), this._bg.height);
  }

  _build(theme) {
    this._buildBg(theme);
    this._buildLabel();
  }

  _buildBg(theme) {
    const bg = makeNinePatch(getWordIndicatorBgPatchConfig(theme));
    this.addChild((this._bg = bg));
  }

  _buildLabel() {
    const label = makeText(getLevelWordIndicatorTextConfig());
    label.anchor.set(0.5);
    this.addChild((this._label = label));
  }
}
