import { InputPriority } from '../constants';
import {
  getCtaEmbeddedPlayButtonPatchConfig,
  getCtaStandardPlayButtonPatchConfig,
  getCtaStandardRetryButtonPatchConfig,
  getMenuButtonDownPatchConfig,
  getMenuButtonPatchConfig,
  getPersistentCtaButtonPatchConfig
} from './ninepatch-configs';
import {
  getCtaStandardPlayButtonTextConfig,
  getCtaStandardRetryButtonTextConfig,
  getMenuButtonTextConfig,
  getPersistentCtaTextConfig
} from './text-configs';

// PERSISTENT CTA
export function getPersistentCtaButtonConfig() {
  return {
    input: {
      name: 'persistent_cta_button',
      priority: InputPriority.Game
    },
    states: {
      up: {
        bg: getPersistentCtaButtonPatchConfig(),
        label: getPersistentCtaTextConfig()
      }
    }
  };
}

// CTA
export function getCtaStandardPlayButtonConfig() {
  return {
    input: {
      name: 'cta_play_button',
      priority: InputPriority.Cta + 1
    },
    states: {
      up: {
        bg: getCtaStandardPlayButtonPatchConfig(),
        label: getCtaStandardPlayButtonTextConfig()
      }
    }
  };
}

export function getCtaEmbeddedPlayButtonConfig() {
  return {
    input: {
      name: 'cta_play_button',
      priority: InputPriority.Cta + 1
    },
    states: {
      up: {
        bg: getCtaEmbeddedPlayButtonPatchConfig(),
        label: getCtaStandardPlayButtonTextConfig()
      }
    }
  };
}

export function getCtaStandardRetryButtonConfig() {
  return {
    input: {
      name: 'cta_retry_button',
      priority: InputPriority.Cta + 1
    },
    states: {
      up: {
        bg: getCtaStandardRetryButtonPatchConfig(),
        label: getCtaStandardRetryButtonTextConfig()
      }
    }
  };
}

export function getMenuButtonConfig(text, width) {
  return {
    input: {
      name: 'menu_btn',
      priority: InputPriority.Game
    },
    states: {
      up: {
        bg: getMenuButtonPatchConfig(width),
        label: getMenuButtonTextConfig(text)
      },
      down: {
        bg: getMenuButtonDownPatchConfig(width),
        label: getMenuButtonTextConfig(text)
      }
    }
  };
}

export function getEmbeddedCTAButtonConfig(text, width) {
  return {
    input: {
      name: 'menu_btn',
      priority: InputPriority.Cta + 1
    },
    states: {
      up: {
        bg: getMenuButtonPatchConfig(width),
        label: getMenuButtonTextConfig(text)
      },
      down: {
        bg: getMenuButtonDownPatchConfig(width),
        label: getMenuButtonTextConfig(text)
      }
    }
  };
}
