import { TUTORIAL_PART_INDEX } from '../../constants';
import { getGameBounds, getViewByProperty, postRunnable } from '../../utils';
import { Container } from '../../utils/container';

export class BlockerView extends Container {
  constructor() {
    super();
    this._build();
  }

  show() {
    this.alpha = 0;
  }

  _build() {
    this._buildCutBlocker();
  }

  _buildCutBlocker() {
    const { x, y, width, height } = getGameBounds();
    const bmd = this.game.add.bitmapData(width, height);
    const blocker = this.game.add.sprite(x, y, bmd);
    this._ctx = bmd.context;
    this._bmd = bmd;
    this.addChild((this._cutBlocker = blocker));
    this._fillBmd(width, height);
    postRunnable(() => {
      for (let i = 0; i < 7; i += 1) {
        this._cutBall(TUTORIAL_PART_INDEX);
      }
    });
    blocker.alpha = 0.5;
  }

  _fillBmd(width, height) {
    this._ctx.beginPath();
    this._ctx.globalCompositeOperation = 'source-over';
    this._ctx.fillStyle = 'rgba(0,0,0,1)';
    this._ctx.rect(0, 0, width, height);
    this._ctx.fill();
    this._ctx.closePath();
  }

  _cutBall(partIndex) {
    const levelView = getViewByProperty('name', 'LevelView');
    const part = levelView.partsComponent.parts[partIndex];
    const { x, y, width, height } = part.getBounds();

    const X = x + width / 2;
    const Y = y + height / 2;

    this._ctx.beginPath();
    this._ctx.globalCompositeOperation = 'destination-out';
    this._ctx.fillStyle = 'rgba(255,255,255,1)';
    this._ctx.arc(X, Y, width / 2, 0, Math.PI * 2, true);
    this._ctx.fill();
    this._ctx.closePath();

    this._ctx.beginPath();
    this._ctx.globalCompositeOperation = 'source-over';
    this._ctx.strokeStyle = 'rgba(0,0,0,0)';
    this._ctx.shadowBlur = 20;
    this._ctx.shadowColor = 'rgba(255, 255, 255, 1)';
    this._ctx.arc(X, Y, width / 2, 0, Math.PI * 2, true);

    this._ctx.closePath();
  }
}
