/* eslint-disable class-methods-use-this */
// eslint-disable-next-line max-classes-per-file
import { getCtaStandardPlayButtonConfig } from '../../configs/button-configs';
import { getCTAStandardGridConfig } from '../../configs/grid-configs';
import { getCtaStandardPopupBgPatchConfig } from '../../configs/ninepatch-configs';
import { getCtaConfettiEmitterConfig } from '../../configs/particles-configs';
import { getCtaStandardPopupTitleTextConfig } from '../../configs/text-configs';
import { GameOverReasons } from '../../constants';
import { fitText, makeEmitter, makeNinePatch, makeText } from '../../utils';
import { Container } from '../../utils/container';
import { CTABaseView } from './cta-base-view';

class CtaPopup extends Container {
  constructor(text) {
    super();

    this._build(text);
  }

  _build(text) {
    this.addChild((this._bg = this._buildBg()));
    this.addChild((this._text = this._buildTitle(text)));
  }

  _buildBg() {
    return makeNinePatch(getCtaStandardPopupBgPatchConfig());
  }

  _buildTitle(text) {
    const tittle = makeText(getCtaStandardPopupTitleTextConfig(text));
    fitText(tittle, this._bg.width - 5, this._bg.height - 10);
    tittle.anchor.set(0.5);
    return tittle;
  }
}

export class CTAStandardView extends CTABaseView {
  getGridConfig() {
    return getCTAStandardGridConfig();
  }

  build(reason = GameOverReasons.Idled) {
    this.alpha = 0;
    super.build(getCtaStandardPlayButtonConfig());
    switch (reason) {
      case GameOverReasons.Idled:
        this._buildPopUp('KEEP PLAYING');
        break;

      case GameOverReasons.ItemsToCtaReached:
        this._buildParticles();
        this._buildPopUp('GREAT JOB');
        this.bringToTop(this.button);
        break;

      default:
        break;
    }

    this.show();
  }

  _buildParticles() {
    makeEmitter(getCtaConfettiEmitterConfig(this, -50));
  }

  _buildPopUp(text) {
    const popup = new CtaPopup(text);
    this.setChild('popup', popup);
  }
}
