/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getMenuButtonConfig } from '../../../configs/button-configs';
import { getMenuGridConfig } from '../../../configs/grid-configs';
import { getMenuTitleTextConfig } from '../../../configs/text-configs';
import { ViewEvents } from '../../../events/view-events';
import { makeText } from '../../../utils';
import { Button } from '../../../utils/button/button';
import { BackgroundComponent } from './background-component';

export class MenuView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    this._buttons = null;

    this.build();
  }

  get name() {
    return 'MenuView';
  }

  get buttons() {
    return this._buttons;
  }

  getGridConfig() {
    return getMenuGridConfig();
  }

  build() {
    super.build(this.getGridConfig());
    this._buildBg();
    this._buildTitleLabel();
  }

  hide() {
    const tw = this.game.add.tween(this).to({ alpha: 0 }, 400, Phaser.Easing.Cubic.InOut, true);
    tw.universal = true;
    tw.onComplete.add(this.destroy, this);
  }

  _buildBg() {
    this._bg = new BackgroundComponent();
    this.setChild('bg', this._bg);
  }

  _buildTitleLabel() {
    const label = makeText(getMenuTitleTextConfig());
    this.setChild('title', label);
  }

  initialize(config) {
    const { categoriesConfig } = config;
    this._buttons = categoriesConfig.map((c, i) => {
      const { id, name } = c;
      const defWidth = 350;
      const btn = new Button(getMenuButtonConfig(name, i === 2 ? 400 : defWidth));
      this.setChild(`button_${i + 1}`, btn);
      btn.onClick.addOnce(this._onButtonClick.bind(this, id), this);

      return btn;
    });
  }

  _onButtonClick(id) {
    this._buttons.forEach(button => {
      button.switchEnable(false);
    });
    lego.event.emit(ViewEvents.MenuView.ButtonClick, id);
  }
}
