export function getLevelBgImageConfig(key) {
  return {
    frame: `bg/bg_${key}.jpg`
  };
}

export function getWordPartBubbleInactiveImageConfig(theme) {
  return {
    frame: `level/themes/${theme}/bubble_bg_inactive.png`
  };
}

export function getWordPartBubbleActiveImageConfig(theme) {
  return {
    frame: `level/themes/${theme}/bubble_bg_active.png`
  };
}

export function getMenuBubbleImageConfig(type) {
  return {
    frame: `menu/bg_bubble_${type}.png`,
    scale: { x: 1, y: 1 }
  };
}

export function getHandImageConfig() {
  return {
    frame: `ui/hand.png`,
    anchor: { x: 0.8, y: 0 }
  };
}
