/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getGameGridConfig } from '../../configs/grid-configs';
import { GameModelState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { LevelView } from './level/level-view';
import { MenuView } from './menu/menu-view';

export class GameView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    this.build(this.getGridConfig());

    this._menuView = null;
    this._levelView = null;

    lego.event.on(ModelEvents.GameModel.LevelsUpdate, this._onLevelsUpdate, this);
    lego.event.on(ModelEvents.GameModel.CurrentLevelIndexUpdate, this._onCurrentLevelUpdate, this);
    lego.event.on(ModelEvents.GameModel.StateUpdate, this._onGameStateUpdate, this);
  }

  get name() {
    return 'GameView';
  }

  getGridConfig() {
    return getGameGridConfig();
  }

  _onCurrentLevelUpdate() {
    if (!CI_API.Globals.PARAMS.skip_intro) {
      this._menuView.initialize(store.game.currentLevel);
    }
  }

  _onLevelsUpdate() {
    if (!CI_API.Globals.PARAMS.skip_intro) {
      this._buildMenu();
    }
  }

  _hideMenu() {
    this._menuView && this._menuView.hide();
  }

  _onGameStateUpdate(state) {
    switch (state) {
      case GameModelState.GamePlay:
        this._hideMenu();
        this._buildLevelView();
        break;

      default:
        break;
    }
  }

  _buildLevelView() {
    this.setChild('level', (this._levelView = new LevelView()));
    this._levelView.show();
  }

  _buildMenu() {
    this.setChild('menu', (this._menuView = new MenuView()));
  }
}
