import { CellAlign, CellScale } from '@armathai/phaser2-grid';
import { getGameBounds } from '../../utils';

export function getMenuGridLandscapeConfig() {
  return {
    // debug: { color: 0x2fc900 },
    name: 'game',
    cells: [
      {
        name: 'bg',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Envelop
      },
      {
        name: 'title',
        bounds: { x: 0.28, y: 0.13, width: 0.6, height: 0.2 }
      },
      {
        name: 'buttons',
        bounds: { x: 0, y: 0.45, width: 1, height: 0.36 },
        cells: [
          {
            name: 'button_1',
            bounds: { x: 0, y: 0, width: 0.55, height: 0.5 },
            align: CellAlign.RightBottom,
            offset: { x: -5 },
            padding: { y: 0.1, height: 0.8 }
          },
          {
            name: 'button_2',
            bounds: { x: 0.55, y: 0, width: 0.45, height: 0.5 },
            align: CellAlign.LeftBottom,
            offset: { x: 5 },
            padding: { y: 0.1, height: 0.8 }
          },
          {
            name: 'button_3',
            bounds: { x: 0.1, width: 0.9 },
            align: CellAlign.CenterTop,
            padding: { y: 0.1, height: 0.8 }
          }
        ]
      }
    ]
  };
}

export function getMenuGridPortraitConfig() {
  const { height: cH } = getGameBounds();
  return {
    // debug: { color: 0x2fc900 },
    name: 'game',
    cells: [
      {
        name: 'bg',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Envelop
      },
      {
        name: 'title',
        bounds: { x: 0, y: 0.3, width: 1, height: 0.13 },
        align: CellAlign.CenterBottom
      },
      {
        name: 'buttons',
        bounds: { x: 0, y: 0.5, width: 1, height: 350 / cH },
        cells: [
          {
            name: 'button_1',
            bounds: { x: 0, y: 0, width: 1, height: 1 / 3 }
          },
          {
            name: 'button_2',
            bounds: { x: 0, width: 1, height: 1 / 3 }
          },
          {
            name: 'button_3',
            bounds: { x: 0, width: 1 }
          }
        ]
      }
    ]
  };
}
