/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getLevelGridConfig } from '../../../configs/grid-configs';
import { getLevelBgImageConfig } from '../../../configs/image-configs';
import { getLevelTitleTextConfig } from '../../../configs/text-configs';
import { TUTORIAL_PART_INDEX, WordIndicatorState } from '../../../constants';
import { ModelEvents } from '../../../events/model-events';
import { ViewEvents } from '../../../events/view-events';
import { store } from '../../../models/store';
import { delayRunnable, makeImage, makeText, postRunnable, removeRunnable } from '../../../utils';
import { PartsComponent } from './parts-component';
import { WordIndicatorComponent } from './word-indicator-component';
import { WordsComponent } from './words-component';

export class LevelView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);
    super.build(this.getGridConfig());

    // this._debugArea = this.game.add.graphics();

    lego.event.on(ModelEvents.LevelModel.CategoryIdUpdate, this._onCategoryIdUpdate, this);
    lego.event.on(ModelEvents.LevelModel.WordsUpdate, this._onWordsUpdate, this);
    lego.event.on(ModelEvents.LevelModel.PartsUpdate, this._onPartsUpdate, this);
    lego.event.on(ModelEvents.LevelModel.WordIndicatorUpdate, this._onWordIndicatorUpdate, this);
    lego.event.on(ModelEvents.WordIndicatorModel.PartsUpdate, this._onWordIndicatorPartsUpdate, this);
    lego.event.on(ModelEvents.PartModel.StateUpdate, this._onPartModelStateUpdate, this);
    lego.event.on(ModelEvents.WordModel.IsCompletedUpdate, this._onWordCompleteUpdate, this);
    lego.event.on(ModelEvents.TutorialSequenceModel.ShowUpdate, this._onTutorialSequenceShowUpdate, this);
    lego.event.on(ModelEvents.TutorialSequenceModel.CompleteUpdate, this._onTutorialSequenceCompleteUpdate, this);
    lego.event.on(ModelEvents.CtaModel.PreVisibleUpdate, this._onCtaPreVisibleUpdate, this);
    lego.event.on(ModelEvents.WordIndicatorModel.StateUpdate, this._onWordIndicatorStateUpdate, this);
  }

  _switchPartsInput(value) {
    this._partsComponent.parts.forEach(part => {
      part.switchInput(value);
    });
  }

  get partsComponent() {
    return this._partsComponent;
  }

  update() {
    super.update();
    this._partsComponent && this.game.physics.arcade.collide(this._partsComponent);
  }

  destroy() {
    removeRunnable(this._stabilizeRunnable);

    lego.event.off(ModelEvents.LevelModel.CategoryIdUpdate, this._onCategoryIdUpdate, this);
    lego.event.off(ModelEvents.LevelModel.WordsUpdate, this._onWordsUpdate, this);
    lego.event.off(ModelEvents.LevelModel.PartsUpdate, this._onPartsUpdate, this);
    lego.event.off(ModelEvents.LevelModel.WordIndicatorUpdate, this._onWordIndicatorUpdate, this);
    lego.event.off(ModelEvents.WordIndicatorModel.PartsUpdate, this._onWordIndicatorPartsUpdate, this);
    lego.event.off(ModelEvents.PartModel.StateUpdate, this._onPartModelStateUpdate, this);
    lego.event.off(ModelEvents.WordModel.IsCompletedUpdate, this._onWordCompleteUpdate, this);
    lego.event.off(ModelEvents.TutorialSequenceModel.ShowUpdate, this._onTutorialSequenceShowUpdate, this);
    lego.event.off(ModelEvents.TutorialSequenceModel.CompleteUpdate, this._onTutorialSequenceCompleteUpdate, this);
    lego.event.off(ModelEvents.WordIndicatorModel.StateUpdate, this._onWordIndicatorStateUpdate, this);

    super.destroy();
  }

  setPhysicsWorld() {
    this.game.physics.startSystem(Phaser.Physics.ARCADE);
    this.game.physics.arcade.enable(this._partsComponent, false);
    this.game.physics.arcade.gravity.y = 900;
    this.game.physics.arcade.checkCollision.up = false;

    this.setPhysicProps();

    this._partsComponent.parts.forEach(part => {
      part.body.velocity.y = 900000 / Math.abs(part.y);
    });

    this._stabilizeRunnable = delayRunnable(1000, () => {
      lego.event.emit(ViewEvents.LevelView.BoardStabilized);
    });
  }

  get name() {
    return 'LevelView';
  }

  setPhysicProps() {
    const { x, y, width, height } = this._partsComponent.getBounds();

    this.game.physics.arcade.setBounds(x, y, width, height);
    this._partsComponent.setPhysicBodies();

    // this._debugArea &&
    //   this._debugArea
    //     .clear()
    //     .beginFill(0xff0000, 0.4)
    //     .drawRect(x, y, width, height);
  }

  getGridConfig() {
    return getLevelGridConfig();
  }

  rebuild(config) {
    if (!this.grid) {
      return;
    }
    this._wordsComponent && this._wordsComponent.rebuild();

    super.rebuild(config);

    this._partsComponent && this.setPhysicProps();
  }

  show() {
    const tw = this.game.add.tween(this).from({ alpha: 0 }, 300, Phaser.Easing.Cubic.InOut, true);
    tw.universal = true;
  }

  _onCtaPreVisibleUpdate(value) {
    if (value) {
      this._switchPartsInput(false);
      this.game.physics.arcade.isPaused = true;
    }
  }

  _onTutorialSequenceShowUpdate() {
    this.game.physics.arcade.isPaused = true;
    this._partsComponent.parts[TUTORIAL_PART_INDEX].switchInput(true);
  }

  _onTutorialSequenceCompleteUpdate() {
    this.game.physics.arcade.isPaused = false;
    this._switchPartsInput(true);
  }

  _onWordIndicatorStateUpdate(state) {
    switch (state) {
      case WordIndicatorState.Alert:
        this._switchPartsInput(false);
        this._wordIndicator.alert().onComplete.add(() => {
          lego.event.emit(ViewEvents.LevelView.AlertComplete);
        });
        break;
      case WordIndicatorState.Idle:
        this._switchPartsInput(true);
        break;

      default:
        break;
    }
  }

  _onCategoryIdUpdate() {
    const { name, theme } = store.game.currentLevel.categoryConfig;

    this._buildBg(theme);
    this._buildTitle(name);
  }

  _onWordsUpdate(words) {
    this._buildWords(words);
  }

  _onPartsUpdate(parts) {
    postRunnable(() => {
      this._buildParts(parts);
    });
  }

  _onWordIndicatorUpdate() {
    this._buildWordIndicator();
  }

  _onWordCompleteUpdate(value, prevValue, uuid) {
    this._wordsComponent.completeWord(uuid);
  }

  _onWordIndicatorPartsUpdate() {
    const { text } = store.game.currentLevel.wordIndicator;
    this._wordIndicator.updateText(text);
    this.setChild('word_indicator', this._wordIndicator);
  }

  _onPartModelStateUpdate(state, prevState, uuid) {
    this._partsComponent.updatePartState(state, uuid);
  }

  _onPartClick(uuid) {
    lego.event.emit(ViewEvents.LevelView.OnPartClick, uuid);
  }

  _buildBg(theme) {
    this._bg = makeImage(getLevelBgImageConfig(theme));
    this.setChild('bg', this._bg);
  }

  _buildTitle(title) {
    this.setChild('title', (this._titleLabel = makeText(getLevelTitleTextConfig(title))));
  }

  _buildWords(words) {
    const { theme } = store.game.currentLevel.categoryConfig;
    this.setChild('words', (this._wordsComponent = new WordsComponent(words, theme)));
  }

  _buildParts(parts) {
    const { theme } = store.game.currentLevel.categoryConfig;
    this.setChild('parts', (this._partsComponent = new PartsComponent(parts, theme)));
    this._partsComponent.onPartClick.add(this._onPartClick, this);
    this.setPhysicsWorld();

    CI_API.Globals.PARAMS.tutorial && this._switchPartsInput(false);
  }

  _buildWordIndicator() {
    const { theme } = store.game.currentLevel.categoryConfig;
    this.setChild('word_indicator', (this._wordIndicator = new WordIndicatorComponent(theme)));
  }
}
