/* eslint-disable class-methods-use-this */
import { getBubblesConfig } from '../../../configs/bubbles-config';
import { getMenuBubbleImageConfig } from '../../../configs/image-configs';
import { makeGradient, makeImage } from '../../../utils';
import { Container } from '../../../utils/container';

export class BackgroundComponent extends Container {
  constructor() {
    super();

    this._build();
  }

  getBounds() {
    return new Phaser.Rectangle(0, 0, 1200, 1200);
  }

  _build() {
    this._buildBackGround();
    this._buildBubbles();
  }

  _buildBackGround() {
    this._menuBg && this._menuBg.destroy();
    this._menuBg = makeGradient({
      width: this.width,
      height: this.height,
      horizontal: 0,
      vertical: 1,
      colors: [
        {
          percent: 0,
          color: '#a541ff'
        },
        {
          percent: 1,
          color: '#3fbbfe'
        }
      ]
    });
    this.addChild(this._menuBg);
  }

  _buildBubbles() {
    this._bubbles = getBubblesConfig();

    this._bubbles.forEach(bubble => {
      const img = makeImage(getMenuBubbleImageConfig(bubble.type));

      img.position.set(bubble.posX, bubble.posY);
      img.scale.set(bubble.scale);
      this.game.add
        .tween(img)
        .to({ x: bubble.toX, y: bubble.toY }, bubble.duration, bubble.easing, true, 0, -1, true).universal = true;

      this.addChild(img);
    });
  }
}
