import { PartState } from '../../constants';
import { ObservableModel } from '../observable-model';

export class PartModel extends ObservableModel {
  constructor(config) {
    super('PartModel');

    this._config = config;
    this._value = config.value;
    this._state = PartState.Idle;

    this.makeObservable('_state');
  }

  get value() {
    return this._value;
  }

  get state() {
    return this._state;
  }

  get config() {
    return this._config;
  }

  collect() {
    this._state = PartState.Collected;
  }

  idle() {
    this._state = PartState.Idle;
  }

  select() {
    this._state = PartState.Selected;
  }
}
