/* eslint-disable class-methods-use-this */
import { getCompletedWordPatchConfig } from '../../../configs/ninepatch-configs';
import { getLevelCompletedWordTextConfig } from '../../../configs/text-configs';
import { makeNinePatch, makeText } from '../../../utils';
import { Container } from '../../../utils/container';

export class WordComponent extends Container {
  constructor(wordModel, radius, xGap, uuid, posConfig, theme) {
    super();

    this._uuid = uuid;
    this._posConfig = posConfig;
    this._circles = null;
    this._completedWord = null;

    this._build(wordModel, radius, xGap, theme);
  }

  get uuid() {
    return this._uuid;
  }

  get posConfig() {
    return this._posConfig;
  }

  complete() {
    this._circles.forEach(circle => {
      circle.visible = false;
    });
    this._completedWord.visible = true;
    this.game.add.tween(this._completedWord.scale).from({ x: 0, y: 0 }, 400, Phaser.Easing.Back.Out, true);
  }

  _build(wordModel, radius, xGap, theme) {
    this._buildCircles(wordModel.full.length, radius, xGap);
    this._buildCompletedWord(wordModel.full, theme);
  }

  _buildCircles(count, radius, xGap) {
    this._circles = [];
    for (let i = 0; i < count; i += 1) {
      const gr = this.game.add.graphics();
      gr.beginFill(0x000000, 0.6);
      gr.drawCircle(0, 0, radius);
      gr.endFill();
      this._circles.push(gr);
      gr.position.set(i * (radius + xGap) + radius / 2, 0);

      this.addChild(gr);
    }
  }

  _buildCompletedWord(word, theme) {
    const { width } = this;
    const label = makeText(getLevelCompletedWordTextConfig(word));
    label.anchor.set(0.5);

    const bg = makeNinePatch(getCompletedWordPatchConfig(label.width + 120, theme));
    bg.anchor.set(0.5);
    bg.position.set(width / 2, 0);

    this.addChild((this._completedWord = bg));
    this._completedWord.addChild(label);
    this._completedWord.visible = false;
  }
}
