import { lego } from '@armathai/lego';
import { GameModelState } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { getViewByProperty, handMoveTw, handScaleTw, searchAtlasByFrame } from '../../utils';
import { sample } from '../../utils/array/sample';

const off = {
  x: 80,
  y: 20
};

export class HintView extends Phaser.Sprite {
  constructor() {
    const texture = searchAtlasByFrame('ui/hand.png');
    super(CI_API.game, 0, 0, texture.key, texture.frame);
    this.anchor.set(0.8, 0);

    lego.event.on(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);

    this._hide();
  }

  destroy() {
    lego.event.off(ModelEvents.HintModel.VisibleUpdate, this._onHintVisibleUpdate, this);

    super.destroy();
  }

  _onHintVisibleUpdate(visible) {
    visible ? this._show() : this._hide();
  }

  update() {
    super.update();
    if (this.visible && this._part) {
      this._setHandPosition();
    }
  }

  _show() {
    switch (store.game.state) {
      case GameModelState.GamePlay:
        this._showOnParts();
        break;
      case GameModelState.ChooseCategory:
        this._showOnMenuButtons();
        break;

      default:
        break;
    }
  }

  _removeHandTw() {
    this.game.tweens.removeFrom(this);
    this.game.tweens.removeFrom(this.scale);
  }

  _getButtonsPositions() {
    const positions = [];
    const menuView = getViewByProperty('name', 'MenuView');
    const { buttons } = menuView;

    buttons.forEach(btn => {
      const { position, parent } = btn;
      const localPos = this.parent.toLocal(position, parent);
      positions.push(localPos);
    });

    return positions;
  }

  _showOnMenuButtons() {
    this._points = this._getButtonsPositions();
    this._removeHandTw();
    this.scale.set(-1, 1);
    this.visible = true;
    this._currentPoint = 0;
    const point = this._points[this._currentPoint];
    this.position.set(point.x + off.x, point.y + off.y);
    this._pointHand();
  }

  _pointHand() {
    handScaleTw(this).onComplete.addOnce(() => {
      this._currentPoint += 1;
      if (this._currentPoint === this._points.length) {
        this._currentPoint = 0;
      }
      this._moveHand(this._points[this._currentPoint]);
    });
  }

  _moveHand(pos) {
    handMoveTw(this, pos, off.x, off.y).onComplete.addOnce(() => {
      this._pointHand();
    });
  }

  _showOnParts() {
    const partsComponent = getViewByProperty('name', 'PartsComponent');
    let partUuid = null;

    const { currentLevel } = store.game;
    const { wordIndicator } = currentLevel;

    if (wordIndicator.parts.length === 0) {
      // No parts selected.....

      const notCompletedWords = currentLevel.getNotCompletedWords();
      const { value } = sample(notCompletedWords).parts[0];
      partUuid = currentLevel.getPartByValue(value).uuid;
    } else if (wordIndicator.parts.length === 1) {
      // One part selected.....

      const p = wordIndicator.parts[0];
      const part = currentLevel.getPartByValue(p.value);
      if (currentLevel.isPartTheStartOfWord(part)) {
        // Right choice....
        const { value } = currentLevel.getWordByPartValue(p.value).parts[1];
        partUuid = currentLevel.getPartByValue(value).uuid;
      } else {
        // Wrong choice....
        partUuid = currentLevel.getPartByValue(p.value).uuid;
      }
    }

    // .......
    this._part = partsComponent.getPartByUuid(partUuid);

    this._setHandPosition();
    this.scale.set(1);
    this.angle = LP(120, 0);
    this.visible = true;
    this.game.tweens.removeFrom(this.scale);
    this.game.add.tween(this.scale).to({ x: 0.8, y: 0.8 }, 500, Phaser.Easing.Cubic.Out, true, 0, -1, true);
  }

  _setHandPosition() {
    const partsComponent = getViewByProperty('name', 'PartsComponent');
    const { position, parent } = this._part;
    const { x, y } = this.parent.toLocal(position, parent);

    const pos = this.parent.toLocal(partsComponent.parent.toGlobal(new Phaser.Point(x, y)), undefined);

    this.position.set(pos.x, pos.y);
  }

  _hide() {
    this.game.tweens.removeFrom(this.scale);
    this.visible = false;
  }
}
