/* eslint-disable class-methods-use-this */
import { PartState } from '../../../constants';
import { makePixel } from '../../../utils';
import { randomReal } from '../../../utils/number/random-real';
import { PartComponent } from './part-component';

export class PartsComponent extends Phaser.Group {
  constructor(parts, theme) {
    super(CI_API.game);

    this._parts = [];
    this._slopes = [];
    this.onPartClick = new Phaser.Signal();

    this._build(parts, theme);
  }

  get name() {
    return 'PartsComponent';
  }

  get parts() {
    return this._parts;
  }

  getBounds() {
    const { x, y } = this.toGlobal(new Phaser.Point(0, 0));

    return new Phaser.Rectangle(x, y, 650 * this.scale.x, 320 * this.scale.y);
  }

  getPartByUuid(uuid) {
    return this._parts.find(part => part.uuid === uuid);
  }

  setPhysicBodies() {
    this._slopes.forEach(slope => {
      slope.body.collideWorldBounds = false;
      slope.body.immovable = true;
      slope.body.allowGravity = false;
    });

    this._parts.forEach(part => {
      part.body.collideWorldBounds = true;
      part.body.bounce.x = 0.1;
      part.body.bounce.y = 0.2;
      part.body.setCircle(75 * this.scale.x);
    });
  }

  updatePartState(state, uuid) {
    const part = this.getPartByUuid(uuid);

    if (!part) {
      return;
    }

    switch (state) {
      case PartState.Selected:
        part.select();
        break;
      case PartState.Idle:
        part.deselect();
        break;
      case PartState.Collected:
        this._destroyPart(part);
        break;

      default:
        break;
    }
  }

  _destroyPart(part) {
    this._parts.splice(this._parts.indexOf(part), 1);
    part.destroy();
  }

  _build(partsModels, theme) {
    this._buildSlope();
    this._buildParts(partsModels, theme);
  }

  _buildSlope() {
    const slopeConfig = [
      { pos: { x: 0, y: 0 } },
      { pos: { x: 32, y: 32 } },
      { pos: { x: 64, y: 64 } },
      { pos: { x: 96, y: 80 } },
      { pos: { x: 618, y: 0 } },
      { pos: { x: 586, y: 32 } },
      { pos: { x: 554, y: 64 } },
      { pos: { x: 522, y: 80 } }
    ];

    this._slopes = slopeConfig.map(config => {
      const {
        pos: { x, y }
      } = config;

      const slope = makePixel({ x, y: y + 220, width: 32, height: 32, alpha: 0 });
      this.addChild(slope);
      return slope;
    });
  }

  _buildParts(partsModels, theme) {
    this._parts = partsModels.map(partModel => {
      const { value, uuid, config } = partModel;
      const { scale, p } = config;
      const part = new PartComponent(uuid, value, theme, scale);
      part.position.set(p.x, p.y - 800);

      part.onClick.add(this._onPartClick, this);

      this.addChild(part);

      return part;
    });
  }

  _onPartClick(uuid) {
    this.getPartByUuid(uuid).body.velocity.x = randomReal(-200, 200);
    this.getPartByUuid(uuid).body.velocity.y = randomReal(-200, 200);

    this.onPartClick.dispatch(uuid);
  }
}
