import { CellScale } from '@armathai/phaser2-grid';

export function getTutorialGridLandscapeConfig() {
  return {
    // debug: { color: 0xff0000 },
    name: 'tutorial',
    cells: [
      {
        name: 'blocker',
        bounds: { x: 0, y: 0, width: 1, height: 1 },
        scale: CellScale.Fill
      },
      {
        name: 'sequence',
        bounds: { x: 0.03, y: 0.35, width: 0.36, height: 0.6 }
      }
    ]
  };
}

