import { lego } from '@armathai/lego';
import { GameModelState } from '../../constants';
import { setCategoryCommand } from '../game/level/set-category-command';
import { setGameStateCommand } from '../game/set-game-state-command';

export function onMenuButtonClickCommand(id) {
  lego.command
    //
    .payload(GameModelState.GamePlay)
    .execute(setGameStateCommand)

    .payload(id)
    .execute(setCategoryCommand);
}
