import { lego } from '@armathai/lego';
import { PartState } from '../../../constants';
import { addPartToWordIndicatorCommand } from './add-part-to-word-indicator-value-command';
import { removePartFromWordIndicatorCommand } from './remove-part-from-word-indicator-value-command';
import { removePartModelCommand } from './remove-part-model-command';

export function onPartStateUpdateCommand(state, prevState, uuid) {
  lego.command
    //
    .payload(uuid)
    .guard(() => state === PartState.Selected)
    .execute(addPartToWordIndicatorCommand)

    .payload(uuid)
    .guard(() => state === PartState.Idle)
    .execute(removePartFromWordIndicatorCommand)

    .payload(uuid)
    .guard(() => state === PartState.Collected)
    .execute(removePartModelCommand);
}
