/* eslint-disable class-methods-use-this */
import { Container } from '../../../utils/container';
import { WordComponent } from './word-component';

export class WordsComponent extends Container {
  constructor(words, theme) {
    super();

    this._words = [];
    this._xGap = 2;
    this._yGap = 15;
    this._digitRadius = 50;

    this._build(words, theme);
    this._setWordsPositions();
  }

  get name() {
    return 'WordsComponent';
  }

  rebuild() {
    this._setWordsPositions();
  }

  getWordByUuid(uuid) {
    return this._words.find(word => word.uuid === uuid);
  }

  completeWord(uuid) {
    const word = this.getWordByUuid(uuid);
    word.complete();
  }

  _build(wordsModels, theme) {
    this._words = wordsModels.map(wordModel => {
      const { config, uuid } = wordModel;
      const { position: posConfig } = config;
      const word = new WordComponent(wordModel, this._digitRadius, this._xGap, uuid, posConfig, theme);

      this.addChild(word);

      return word;
    });
  }

  _setWordsPositions() {
    this._words.forEach(word => {
      const { l, p } = word.posConfig;
      const { yOffset, xOffset } = LP(l, p);
      word.position.set(xOffset * (this._digitRadius + this._xGap), yOffset * (this._digitRadius + this._yGap));
    });
  }
}
