/* eslint-disable class-methods-use-this */
import { lego } from '@armathai/lego';
import { Phaser2Grid } from '@armathai/phaser2-grid';
import { getCTAGridConfig } from '../../configs/grid-configs';
import { AdStatus, GameOverReasons } from '../../constants';
import { ModelEvents } from '../../events/model-events';
import { store } from '../../models/store';
import { CTAEmbeddedView } from './cta-embedded-view';
import { CTAStandardView } from './cta-standard-view';

export class CTAView extends Phaser2Grid {
  constructor() {
    super(CI_API.game);

    lego.event.on(ModelEvents.AdModel.StatusUpdate, this._onAdStatusUpdate, this);
  }

  getGridConfig() {
    return getCTAGridConfig();
  }

  rebuild(config = this.getGridConfig()) {
    if (!this.grid) {
      return;
    }
    super.rebuild(config);
  }

  build() {
    super.build(this.getGridConfig());
    const { reason } = store.ad.cta;
    if (reason === GameOverReasons.Idled) {
      this.setChild('cta', (this._content = new CTAStandardView()));
      this._content.build(reason);
    } else {
      switch (CI_API.Globals.PARAMS.cta_type) {
        case 'standard':
          this.setChild('cta', (this._content = new CTAStandardView()));
          this._content.build(reason);
          break;

        case 'embedded':
          (() => {
            this.setChild('cta', (this._content = new CTAEmbeddedView()));
            this._content.build();
          })();
          break;

        default:
          break;
      }
    }
  }

  _onAdStatusUpdate(status) {
    // this._content.onAdStatusUpdate(status);
    switch (status) {
      case AdStatus.Cta:
        this.build();
        break;
      case AdStatus.Game:
        this.removeChildren();
        break;
      default:
    }
  }
}
